@import url("https://use.fontawesome.com/releases/v5.5.0/css/all.css");
@import url("https://fonts.googleapis.com/css?family=Cormorant+SC:400,500|Montserrat:300,400,500");
*,
*:before,
*::after {
  margin: 0;
  padding: 0; }

:root {
  --font-size-initial: 12px;
  --font-size-smartphone-large: 13px;
  --font-size-phablet: 14px;
  --font-size-tablet-small: 15px;
  --font-size-tablet-large: 16px;
  --font-size-laptop: 18px;
  --font-size-desktop: 20px;
  --desktop-size: 1600px;
  --laptop-size: 1024px;
  --tablet-large-size: 1600px;
  --tablet-small-size: 960px;
  --phablet-size: 640px;
  --smartphone-large-size: 480px;
  --smartphone-small-size: 320px; }

html {
  font-size: var(--font-size-initial, 12px);
  font-family: "Montserrat", sans-serif; }

li {
  list-style-type: none outside; }

a {
  text-decoration: none;
  color: black; }

@media screen and (min-width: 480px) {
  html {
    font-size: var(--font-size-smartphone-large, 13px); } }

@media screen and (min-width: 640px) {
  html {
    font-size: var(--font-size-phablet, 14px); }
  .site {
    padding: 1rem; } }

@media screen and (min-width: 960px) {
  html {
    font-size: var(--font-size-tablet-small, 15px); }
  .site {
    padding: 2rem; } }

@media screen and (min-width: 960px) {
  html {
    font-size: var(--font-size-tablet-large, 16px); }
  .site {
    padding: 3rem; } }

@media screen and (min-width: 1024px) {
  html {
    font-size: var(--font-size-laptop, 18px); }
  .site {
    padding: 4rem; } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-desktop, 20px); } }
